.product-subpage {
  width: 100%;
  padding-top: 100px;
}

.product-name {
  text-align: start;
  font-size: 20px;
  font-weight: 500;
}

.product-detail {
  font-size: 17px;
}

.p-img {
  width: 100%;
}

.quantity-price {
  text-align: start;
}

.quantity {
  display: flex;
  align-items: center;
}

.quantity-plus-minus {
  font-size: 30px;
  margin: 0;
  cursor: pointer;
}

.minus {
  margin-left: 30px;
}

.quantity-number {
  padding: 5px 20px;
  color: #000;
  background-color: #bababa80;
  margin: 0 20px;
}

.product-price {
  display: flex;
  margin-bottom: 30px;
}

.p-price {
  margin-left: 30px;
}

.p-add-to-cart {
  display: block;
  width: 100%;
  text-align: center;
  text-decoration: none;
  border: 2px solid #000;
  padding: 5px 0;
  color: #000;
  background-color: #bababa80;
}

@media all and (min-width: 450px) {
  .p-img {
    width: 60%;
  }
}

@media all and (min-width: 700px) {
  .product-subpage {
    padding-top: 80px;
  }

  .product-name {
    text-align: start;
    font-size: 25px;
  }

  .product-detail {
    display: flex;
    font-size: 20px;
  }

  .product-img {
    width: 50%;
  }

  .p-img {
    width: 100%;
  }

  .quantity-price {
    width: 50%;
    text-align: end;
  }

  .quantity {
    justify-content: flex-end;
  }

  .product-price {
    justify-content: flex-end;
  }

  .p-add-to-cart {
    display: inline;
    padding: 5px 50px;
  }
}
